.root {
    flex-grow: 1;
}

.logo {
    flex-grow: 1;
    text-shadow: 2px 1px rgba(0,0,0, 0.35);
}

.title {
    flex-grow: 1;
    text-shadow: 2px 1px rgba(0,0,0, 0.35);
}

.menuButton {
    margin-right: 16px;
    text-shadow: 2px 1px rgba(0,0,0, 0.35);
}

@media (min-width: 600px) {
    .menuButton {
        display: none;
    }
}

.drawer {
    width: 240px;
    flex-shrink: 0;
}

.drawerPaper {
    width: 240px;
}

@media (max-width: 599px) {
    .drawer {
        width: 100%;
        flex-shrink: 0;
    }
}