.main {
    margin: 0;
    /*background-image: url("/public/blueplanet.png");
    filter: blur(5px);*/
}

.landingpage-section {
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-direction: column;
    flex-flow: column;
    width: 100%;
    background-color: #031428;
}
.bg-image {
    background-image: url("/public/blueplanet.png");
    width: 100%;
    height: 100vh;
    /* Center and scale the image nicely */
    background-position: bottom;
    background-repeat: no-repeat;
    background-size: cover;
}

.hero-text {
    text-shadow:
    1px 4px 4px rgba(0,0,0,0.25),
    0px 0px 7px rgba(0,0,0,0.25),
    0px 0px 12px rgba(0,0,0,0.25),
    0px 0px 15px rgba(0,0,0,0.15);
    
}

.hero-text-secondary {
    text-shadow: 
    1px 4px 4px rgba(0,0,0,0.15),
    0px 0px 7px rgba(0,0,0,0.15),
    0px 0px 12px rgba(0,0,0,0.15),
    0px 0px 15px rgba(0,0,0,0.15);
    color: white;
}

.section {
    background: radial-gradient(circle, rgba(236,214,240,1) 0%, rgba(3,20,40,1) 100%);
    height: 100px;
}